@mixin padding-leftPortrait($min, $max) {
    padding-left: calc(#{$min}rem + (((100vw - #{$bp-small-portraitWidth/1em*1.6rem}) / (#{$bp-large-portraitWidth/1em * 1.6} - #{$bp-small-portraitWidth/1em * 1.6}))) * (#{$max} - #{$min}));
