/* =============================================================================================================================
                                                    Landscape Mixins ENDS
============================================================================================================================== */
@mixin buttonReset {
    background: transparent;
    border: none;
    outline: none;
}

@mixin sectionBanner($width, $height, $border, $fontSize, $lineHeight, $content, $absolute) {
    @if($absolute==yes) {
        @include absoluteCenter;
    }

    @else {
        position: relative;
        left: 50%;
        transform: translateX(-50%);
    }

    @include flex-center;
    width: $width;
    height: $height;
    background: rgba(134, 112, 112, 0.76);
    border: $border solid #FFFFFF;
    color: white;

    &::after {
        content: "#{$content}";
        font-size: $fontSize;
        line-height: $lineHeight;
        font-family: $fontAbril;
        font-weight: 400;
        text-transform: uppercase;
    }
}

@mixin absoluteCenter {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@mixin flex-spaceBetweenCenter {
    display: flex;
    justify-content: space-between;
    align-items: center;
}



/* Box */
@mixin box($width, $height: $width) {
    width: $width;
    height: $height;
}

/* ===== Usage ===== */
/* div {
    // You can pass width && height
    @include box(200px, 300px);
    //or just pass width and the height
    //will default to the width value
    @include box(200px);
  } */

/* Flexbox Toolkit */
@mixin flex-column {
    display: flex;
    flex-direction: column;
}

@mixin flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

@mixin flex-center-column {
    @include flex-center;
    flex-direction: column;
}

@mixin flex-center-vert {
    display: flex;
    align-items: center;
}

@mixin flex-center-horiz {
    display: flex;
    justify-content: center;
}

