/* ANIMATIONS */


@mixin animation($str) {
    animation: #{$str};
}

@mixin bounceAnimation($delay) {
    animation: bounceAlpha 1.4s #{$delay}s infinite;
}

@keyframes bounceAlpha {
    0% {
        opacity: 1;
        transform: translateY(0px) scale(1);
    }

    25% {
        opacity: 0;
        transform: translateY(10px) scale(0.9);
    }

    26% {
        opacity: 0;
        transform: translateY(-10px) scale(0.9);
    }

    55% {
        opacity: 1;
        transform: translateY(0px) scale(1);
    }
}

@keyframes transitionImg {
    from {
        filter: blur(10px);
        opacity: 0;
    }

    to {
        filter: blur(0px);
        opacity: 1;
    }
}